import React, { Component } from 'react';
import 'tachyons';

class SignIn extends Component {
  constructor(props) {
    super(props);
    this.state = {
      signInEmail: '',
      signInPassword: ''
    }
  }

  handleEmailChange = (event) => {
    this.setState({signInEmail: event.target.value})
  }

  handlePasswordChange = (event) => {
    this.setState({signInPassword: event.target.value})
  }

  handleSignInSubmit = (event) => {
    event.preventDefault();
    fetch('https://cryptic-falls-77467.herokuapp.com/signin', {
      method: 'post',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        email: this.state.signInEmail,
        password: this.state.signInPassword
      })
    })
      .then(response => response.json())
      .then(user => {
        if (user.id) {
          this.props.loadUser(user);
          this.props.handleRouteChange('home');
        }
      })
  }

  render() {
    const { handleRouteChange } = this.props;
    return (
      <article className="br3 ba b--black-10 mmv4 w-100 w-50-m w-25-1 mw6 shadow-5 center">
        <main className="pa4 black-80">
          <div className="measure">
            <form onSubmit={this.handleSignInSubmit}>

            <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
              <legend className="f1 fw6 ph0 mh0">Sign In</legend>
              <div className="email mt3">
                <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
                <input
                  className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100"
                  type="email"
                  name="email-address"
                  id="email-address"
                  onChange={this.handleEmailChange}
                />
              </div>
              <div className="password mv3">
                <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
                <input
                  className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100"
                  type="password"
                  name="password"
                  id="password"
                  onChange={this.handlePasswordChange}
                />
              </div>
            </fieldset>
            <div className="">
              <input
                className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib"
                type="submit"
                value="Sign in"
              />
            </div>
          </form>
            <div className="lh-copy mt3">
              <p className="f6 link dim black db pointer" onClick={() => handleRouteChange('register')}>Register</p>
            </div>
          </div>
        </main>
      </article>
    )
  }
}

export default SignIn;
